.form {
  display: flex;
  align-items: center;
}

.input {
  padding: 12px 14px;
  cursor: pointer;
  border-radius: 3px;
  border: 3px solid black;
  font-weight: 500;
  margin-right: 10px;
  outline: none;
}

.button {
  padding: 10px 10px;
  cursor: pointer;
  border-radius: 6px;
  border: 3px solid black;
  font-size: 20px;
  font-weight: 500;
}

.button:hover {
  color: white;
  background-color: blue;
}
